import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import $ from 'jquery'
import GAuth from './middleware/gauth.js'
import axios from 'axios'
import moment from 'moment'

const googleKey = require('./google.key.json')

const access = Vue.prototype.$access = (bool, msg, url = false) => {
  if (!bool) {
    alert(msg)
    if (url) {
      router.push(url)
    } else {
      router.go(-1)
    }
  }
}

Vue.prototype.digit = num => {
  num = ('0' + num).slice(-2)
  return num
}

Vue.prototype.$memberChk = () => { access(store.state.member, '  ', '/member/login') }
Vue.prototype.$notMemberChk = () => { access(!store.state.member, '   ') }
Vue.prototype.contentPreview = (text, len) => {
  if (text.length > len) {
    text = text.substring(0, len) + '...'
  }
  return text
}
Vue.prototype.moment = moment
Vue.prototype.getDateFormat = time => {
  const date = new Date(time)
  return moment(date).format('YYYY-MM-DD HH:mm')
}
const getFlowDate = Vue.prototype.getFlowDate = time => {
  const computedTime = +new Date() - (+new Date(time))
  const abs = Math.abs(computedTime)
  const s = 60 * 1000
  const i = s * 60
  const h = i * 24
  let newTime
  if (abs < s) {
    newTime = ~~(computedTime / 1000) + ''
  } else if (abs < i) {
    newTime = ~~(computedTime / s) + ''
  } else if (abs < h) {
    newTime = ~~(computedTime / i) + ''
  } else {
    newTime = ~~(computedTime / h) + ''
  }
  return newTime
}
Vue.prototype.$http = axios
Vue.prototype.getRange = (start_date, limit_date) => {
  const start = moment(start_date).format('MM. DD HH:mm')
  const limit = moment(limit_date).format('MM. DD HH:mm')
  return `${start} ~ ${limit}`
}
Vue.prototype.getRemaining = (date, toString = true) => {
  const toSecond = new Date(date)
  if (!toString) return toSecond - (+new Date())
  const remaining = getFlowDate(toSecond)
  return parseInt(remaining) > 0 ? `${remaining} ` : `${remaining.replace('-', '')} `
}

Vue.prototype.getApiData = async (promise) => {
  const response = await promise
  const data = response.data
  if (!data.success) throw data.err
  return data
}
Vue.prototype.getGoogleConfig = () => googleKey.config

Vue.use(GAuth, googleKey.config)

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

$(document)
  .on('submit', 'form', () => false)
  .on('click', 'a[href="#"]', () => false)